.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}
#danface{
  position:absolute;
}
#mapcanvas{
  position: absolute;
  /* width: 100%; */
  /* height:100%; */
  left:0;
  pointer-events: none;
}
path {
  transition: 500ms;
  fill:green;
  stroke: rgba(250,250,250,1);
  stroke-width:1px;
  stroke-linejoin: round;
}
#mapContainer{
  position: relative;
  width: fit-content;
  margin: auto;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
